import React from "react";
import hostMap from "../hostMap";
import fetch from "../fetch";
import WujieReact from "@tencent/wujie-react";
import { useNavigate } from "react-router-dom";
import lifecycles from "../lifecycle";

export default function React16() {
  const navigation = useNavigate();
  const react16Url = hostMap("//localhost:7600");
  const react17Url = hostMap("//localhost:7100");
  const vue2Url = hostMap("//localhost:7200");
  const vue3Url = hostMap("//localhost:7300");
  const vite = hostMap("//localhost:7500");
  const angular12Url = hostMap("//localhost:7400");
  const degrade = window.localStorage.getItem("degrade") === "true";
  const vue3Fetch = (url, options) =>
    url.includes(hostMap("//localhost:7300")) ? fetch(url, options) : window.fetch(url, options);
  const props = {
    jump: (name) => {
      navigation(`/${name}`);
    },
  };
  return (
    <div style={{ height: "100%", width: "100%" }}>
      <div className="all-item">
        <WujieReact
          height="100%"
          width="100%"
          name="react16"
          url={react16Url}
          sync={true}
          fetch={fetch}
          props={props}
          degrade={degrade}
          prefix={{ "prefix-dialog": "/dialog", "prefix-location": "/location" }}
          beforeLoad={lifecycles.beforeLoad}
          beforeMount={lifecycles.beforeMount}
          afterMount={lifecycles.afterMount}
          beforeUnmount={lifecycles.beforeUnmount}
          afterUnmount={lifecycles.afterUnmount}
          activated={lifecycles.activated}
          deactivated={lifecycles.deactivated}
          loadError={lifecycles.loadError}
        ></WujieReact>
      </div>
      <div className="all-item">
        <WujieReact
          height="100%"
          width="100%"
          name="react17"
          url={react17Url}
          sync={true}
          fetch={fetch}
          props={props}
          alive={true}
          degrade={degrade}
          beforeLoad={lifecycles.beforeLoad}
          beforeMount={lifecycles.beforeMount}
          afterMount={lifecycles.afterMount}
          beforeUnmount={lifecycles.beforeUnmount}
          afterUnmount={lifecycles.afterUnmount}
          activated={lifecycles.activated}
          deactivated={lifecycles.deactivated}
          loadError={lifecycles.loadError}
        ></WujieReact>
      </div>
      <div className="all-item">
        <WujieReact
          height="100%"
          width="100%"
          name="vue2"
          url={vue2Url}
          sync={true}
          fetch={fetch}
          props={props}
          degrade={degrade}
          beforeLoad={lifecycles.beforeLoad}
          beforeMount={lifecycles.beforeMount}
          afterMount={lifecycles.afterMount}
          beforeUnmount={lifecycles.beforeUnmount}
          afterUnmount={lifecycles.afterUnmount}
          activated={lifecycles.activated}
          deactivated={lifecycles.deactivated}
          loadError={lifecycles.loadError}
        ></WujieReact>
      </div>
      <div className="all-item">
        <WujieReact
          height="100%"
          width="100%"
          name="vue3"
          url={vue3Url}
          sync={true}
          fetch={vue3Fetch}
          props={props}
          alive={true}
          degrade={degrade}
          beforeLoad={lifecycles.beforeLoad}
          beforeMount={lifecycles.beforeMount}
          afterMount={lifecycles.afterMount}
          beforeUnmount={lifecycles.beforeUnmount}
          afterUnmount={lifecycles.afterUnmount}
          activated={lifecycles.activated}
          deactivated={lifecycles.deactivated}
          loadError={lifecycles.loadError}
        ></WujieReact>
      </div>
      <div className="all-item">
        <WujieReact
          height="100%"
          width="100%"
          name="vite"
          url={vite}
          sync={true}
          fetch={fetch}
          props={props}
          degrade={degrade}
          beforeLoad={lifecycles.beforeLoad}
          beforeMount={lifecycles.beforeMount}
          afterMount={lifecycles.afterMount}
          beforeUnmount={lifecycles.beforeUnmount}
          afterUnmount={lifecycles.afterUnmount}
          activated={lifecycles.activated}
          deactivated={lifecycles.deactivated}
          loadError={lifecycles.loadError}
        ></WujieReact>
      </div>
      <div className="all-item">
        <WujieReact
          height="100%"
          width="100%"
          name="angular12"
          url={angular12Url}
          sync={true}
          fetch={fetch}
          props={props}
          degrade={degrade}
          beforeLoad={lifecycles.beforeLoad}
          beforeMount={lifecycles.beforeMount}
          afterMount={lifecycles.afterMount}
          beforeUnmount={lifecycles.beforeUnmount}
          afterUnmount={lifecycles.afterUnmount}
          activated={lifecycles.activated}
          deactivated={lifecycles.deactivated}
          loadError={lifecycles.loadError}
        ></WujieReact>
      </div>
    </div>
  );
}
